<template>
  <div>
      <!-- 面包屑导航区 -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>商品管理</el-breadcrumb-item>
        <el-breadcrumb-item>参数列表</el-breadcrumb-item>
      </el-breadcrumb>
      <!-- 卡片视图区域 -->
      <el-card>
          <!-- 警告区域 -->
          <el-alert show-icon
            title="注意：只允许为第三级分类设置相关参数！"
            type="warning" :closable="false">
          </el-alert>
          <!-- 选择商品分类区域 -->
          <el-row class="cat_opt">
              <el-col>
                  <span>选择商品分类：</span>
                  <!-- 选择商品分类的级联选择框 -->
                   <el-cascader
                    v-model="selectedCateKeys"
                    :options="cateList"
                    :props="{ expandTrigger: 'hover',value:'cat_id',label:'cat_name',children:'children' }"
                    @change="handleChange"></el-cascader>
              </el-col>
          </el-row>
          <!-- tab页签区域 -->
          <el-tabs v-model="activeName" @tab-click="handleTabClick">
            <!-- 添加动态参数的面板 -->
            <el-tab-pane label="动态参数" name="many">
                <!-- 添加参数的按钮 -->
                <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible = true">添加参数</el-button>
                <!-- 动态参数表格 -->
                <el-table :data="manyTableDate" border stripe> 
                    <!-- 展开行 -->
                    <el-table-column type="expand">
                        <template v-slot="scope">
                            <!-- 循环渲染tag标签 -->
                            <el-tag v-for="(item,i) in scope.row.attr_vals" :key="i" closable @close="handleClose(i,scope.row)">{{item}}</el-tag>
                            <!-- 输入的文本框 -->
                            <el-input
                            class="input-new-tag"
                            v-if="scope.row.inputVisible"
                            v-model="scope.row.inputValue"
                            ref="saveTagInput"
                            size="small"
                            @keyup.enter.native="handleInputConfirm(scope.row)"
                            @blur="handleInputConfirm(scope.row)"
                            >
                            </el-input>
                            <!-- 添加的按钮 -->
                            <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                        </template>
                    </el-table-column>
                    <!-- 索引列 -->
                    <el-table-column type="index" label="#"></el-table-column>
                    <el-table-column label="参数名称" prop="attr_name"></el-table-column>
                    <el-table-column label="操作" width="300">
                        <template v-slot="scope">
                            <!-- 编辑按钮 -->
                                <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.attr_id)">编辑</el-button>
                            <!-- 删除按钮 -->
                                <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeParams(scope.row.attr_id)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-tab-pane>
            <!-- 添加静态属性的面板 -->
            <el-tab-pane label="静态属性" name="only">
                <!-- 添加属性的按钮 -->
                <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible = true">添加属性</el-button>
                <!-- 静态属性表格 -->
                <el-table :data="onlyTableDate" border stripe> 
                    <!-- 展开行 -->
                    <el-table-column type="expand">
                        <template v-slot="scope">
                            <!-- 循环渲染tag标签 -->
                            <el-tag v-for="(item,i) in scope.row.attr_vals" :key="i" closable @close="handleClose(i,scope.row)">{{item}}</el-tag>
                            <!-- 输入的文本框 -->
                            <el-input
                            class="input-new-tag"
                            v-if="scope.row.inputVisible"
                            v-model="scope.row.inputValue"
                            ref="saveTagInput"
                            size="small"
                            @keyup.enter.native="handleInputConfirm(scope.row)"
                            @blur="handleInputConfirm(scope.row)"
                            >
                            </el-input>
                            <!-- 添加的按钮 -->
                            <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                        </template>
                    </el-table-column>
                    <!-- 索引列 -->
                    <el-table-column type="index" label="#"></el-table-column>
                    <el-table-column label="属性名称" prop="attr_name"></el-table-column>
                    <el-table-column label="操作" width="300">
                        <template v-slot="scope">
                            <!-- 编辑按钮 -->
                                <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.attr_id)">编辑</el-button>
                            <!-- 删除按钮 -->
                                <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeParams(scope.row.attr_id)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-tab-pane>
          </el-tabs>
      </el-card>
      <!-- 添加参数的对话框 -->
      <el-dialog
        :title="'添加'+titleText"
        :visible.sync="addDialogVisible"
        width="50%" @close="addDialogColsed">
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
            <el-form-item :label="titleText" prop="attr_name">
                <el-input v-model="addForm.attr_name"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="addDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="addParams">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 添加编辑的对话框 -->
      <el-dialog
        :title="'编辑'+titleText"
        :visible.sync="editDialogVisible"
        width="50%" @close="editDialogColsed">
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
            <el-form-item :label="titleText" prop="attr_name">
                <el-input v-model="editForm.attr_name"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="editDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="editParams">确 定</el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
export default {
    data(){
        return{
            //商品分类列表
            cateList: [],
            //级联选择框双向绑定到的数组
            selectedCateKeys: [],
            //被激活的页签名称
            activeName: 'many',
            //动态参数的数据
            manyTableDate: [],
            //静态属性的数据
            onlyTableDate: [],
            //控制添加对话框的显示与隐藏
            addDialogVisible: false,
            //添加参数的表单数据对象
            addForm: {
                attr_name: ''
            },
            //添加表单的验证规则对象
            addFormRules: {
                attr_name: [{
                    required: true,message: '请输入参数名称',trigger: 'blur'
                }]
            },
            //控制编辑对话框的显示与隐藏
            editDialogVisible: false,
            //编辑参数的表单数据对象
            editForm: {
                attr_name: ''
            },
            //编辑表单的验证规则对象
            editFormRules: {
                attr_name: [{
                    required: true,message: '请输入参数名称',trigger: 'blur'
                }]
            }
        }
    },
    created(){
        this.getCateList()
    },
    methods: {
        async getCateList(){
            const {data:res} = await this.$http.get('categories')
            if(res.meta.status !== 200){
                return this.$message.error('获取商品分类失败')
            }
            this.cateList = res.data
            // console.log(this.cateList);
        },
        //级联选择框选中项变化会触发这个函数
        handleChange(){
            this.getParamsData()
        },
        //Tab页签点击事件的处理函数
        handleTabClick(){
            this.getParamsData()
        },
        //获取参数的列表数据
        async getParamsData(){
            //证明选中的不是三级分类
            if(this.selectedCateKeys.length !== 3){
                this.selectedCateKeys = []
                this.manyTableDate = []
                this.onlyTableDate = []
                return
            }
            //选中了三级分类
            // console.log(this.selectedCateKeys);
            //根据所选分类的id和当前所处的面板获取对应的参数
            const {data:res} = await this.$http.get(`categories/${this.cateId}/attributes`,{params: {sel:this.activeName}})
            if(res.meta.status !== 200){
                return this.$message.error('获取参数列表失败')
            }
            // console.log(res.data);
            res.data.forEach(item => {
                item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
                //控制文本框的显示与隐藏
                item.inputVisible = false
                //文本框中输入的值
                item.inputValue = ''
            })
            // console.log(res.data);
            if(this.activeName === 'many'){
                this.manyTableDate = res.data
            } else {
                this.onlyTableDate = res.data
            }
        },
        //监听添加对话框的关闭事件
        addDialogColsed(){
            this.$refs.addFormRef.resetFields()
        },
        //点击按钮添加参数
        addParams(){
            this.$refs.addFormRef.validate(async valid => {
                if(!valid) return
                const {data:res} = await this.$http.post(`categories/${this.cateId}/attributes`,{
                    attr_name: this.addForm.attr_name,
                    attr_sel: this.activeName
                })
                if(res.meta.status !== 201){
                    return this.$message.error('添加参数失败')
                }
                this.$message.success('添加参数成功')
                this.addDialogVisible = false
                this.getParamsData()
            })
        },
        //点击按钮，展示编辑的对话框
        async showEditDialog(attr_id){
            //查询当前参数的信息
            const {data:res} = await this.$http.get(`categories/${this.cateId}/attributes/${attr_id}`,{
                params:{attr_sel: this.activeName}
            })
            if(res.meta.status !== 200){
                return this.$message.error('获取参数信息失败')
            }
            this.editForm = res.data
            this.editDialogVisible = true
        },
        //重置修改的表单
        editDialogColsed(){
            this.$refs.editFormRef.resetFields()
        },
        //点击按钮修改参数信息
        editParams(){
            this.$refs.editFormRef.validate(async valid => {
                if(!valid) return
                const {data:res} = await this.$http.put(`categories/${this.cateId}/attributes/${this.editForm.attr_id}`,{
                    attr_name: this.editForm.attr_name,
                    attr_sel: this.activeName
                })
                if(res.meta.status !== 200){
                    return this.$message.error('修改参数失败')
                }
                this.$message.success('修改参数成功')
                this.getParamsData()
                this.editDialogVisible = false
            })
        },
        //根据id删除对应的参数项
        async removeParams(attr_id){
            const confirmResult = await this.$confirm('此操作将永久删除该参数, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
        }).catch(err => err)
        //用户取消了删除的操作
        if(confirmResult !== 'confirm'){
            return this.$message.info('取消了删除')
        }
        //删除的业务逻辑
           const {data:res} = await this.$http.delete(`categories/${this.cateId}/attributes/${attr_id}`)
           if(res.meta.status !== 200){
               return this.$message.error('删除参数失败')
           }
           this.$message.success('删除参数成功')
           this.getParamsData()
        },
        //文本框失去焦点或按下回车时触发
        async handleInputConfirm(row){
            if(row.inputValue.trim().length === 0){
                row.inputValue = ''
                row.inputVisible = false
                return
            }
            this.saveAttrVals(row,'add')
        },
        //点击按钮显示文本输入框
        showInput(row){
            row.inputVisible = true 
            this.$nextTick(() => {
          this.$refs.saveTagInput.$refs.input.focus();
        })          
        },
        async saveAttrVals(row,flag){
            const msg = flag === 'add' ? '添加' : '删除'
            if(flag === 'add'){
                
                row.attr_vals.push(row.inputValue.trim())
                row.inputValue = ''
                row.inputVisible = false
            }
            const {data:res} = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`,{
                attr_name: row.attr_name,
                attr_sel: row.attr_sel,
                attr_vals: row.attr_vals.join(' ')
            })
            
            if(res.meta.status !== 200){
                row.attr_vals.pop()
                return this.$message.error(`${msg}参数项失败`)
            }
            
            this.$message.success(`${msg}参数项成功`)
            // console.log(res.data);
        },
        handleClose(i,row){
            row.attr_vals.splice(i,1)
            // console.log(row.attr_vals);
            this.saveAttrVals(row,'detele')
        }
    },
    computed: {
        //如果按钮需要被禁用，则返回true,否则返回false
        isBtnDisabled(){
            if(this.selectedCateKeys.length !== 3){
                return true
            }
            return false
        },
        //当前选中的三级分类的id
        cateId(){
            if(this.selectedCateKeys.length === 3){
                return this.selectedCateKeys[2]
            }
            return null
        },
        //动态计算标题的文本
        titleText(){
            if(this.activeName === 'many'){
                return '动态参数'
            }
            return '静态属性'
        }
    }
}
</script>

<style lang="less" scoped>
    .cat_opt{
        margin: 15px 0;
    }
    .el-tag{
        margin: 0 5px;
    }
    .input-new-tag{
        width: 100px;
    }
    .button-new-tag{
        width: 100px;
    }
</style>